<template>
  <v-app>
    <v-main class="secondary lighten-1">
      <v-container>
        <v-card class="d-flex flex-row justify-center mt-2 mx-auto pt-2 pb-2 secondary" style="width: 80%">
          <a href="http://telegram.me/kakaei1989" style="text-decoration: none" class="mt-5">
            <v-avatar color="info" size="30px">
              <v-icon color="white">mdi-telegram</v-icon>
            </v-avatar>
          </a>

          <v-avatar size="70px" class="mx-3">
            <v-img
                src="kakaei.jpg"
                @click="$refs.profile.click()"
            ></v-img>
          </v-avatar>
          <a class="Header-link mt-5" href="https://github.com/kakaei1989/slideshow" data-hotkey="g d" aria-label="Homepage "
             data-ga-click="Header, go to dashboard, icon:logo">
            <svg class="octicon octicon-mark-github v-align-middle" height="32" viewBox="0 0 16 16"
                 version="1.1" width="32" aria-hidden="true">
              <path fill-rule="evenodd"
                    d="M8 0C3.58 0 0 3.58 0 8c0 3.54 2.29 6.53 5.47 7.59.4.07.55-.17.55-.38 0-.19-.01-.82-.01-1.49-2.01.37-2.53-.49-2.69-.94-.09-.23-.48-.94-.82-1.13-.28-.15-.68-.52-.01-.53.63-.01 1.08.58 1.23.82.72 1.21 1.87.87 2.33.66.07-.52.28-.87.51-1.07-1.78-.2-3.64-.89-3.64-3.95 0-.87.31-1.59.82-2.15-.08-.2-.36-1.02.08-2.12 0 0 .67-.21 2.2.82.64-.18 1.32-.27 2-.27.68 0 1.36.09 2 .27 1.53-1.04 2.2-.82 2.2-.82.44 1.1.16 1.92.08 2.12.51.56.82 1.27.82 2.15 0 3.07-1.87 3.75-3.65 3.95.29.25.54.73.54 1.48 0 1.07-.01 1.93-.01 2.2 0 .21.15.46.55.38A8.013 8.013 0 0016 8c0-4.42-3.58-8-8-8z"></path>
            </svg>
          </a>
        </v-card>
        <v-row style="width: 80%" class="mx-auto mt-2">
          <v-card>
            <v-img :src="items[sliderIndex]+'.jpg'" height="400px" width="100%">
              <div class="d-flex flex-row justify-center">
                <v-btn class="info" style="position: absolute;top: 50%;right: 0" @click="next">next</v-btn>
                <v-spacer></v-spacer>
                <v-btn class="success" style="position: absolute;top: 50%;left: 0" @click="prev">prev</v-btn>
              </div>
            </v-img>
          </v-card>
        </v-row>
        <v-row style="width: 80%" class="mx-auto">
          <div class="flex d-flex flex-row justify-space-around"
               v-for="(item,index) in items"
               @click="sliderIndex=index">
            <v-btn style="color: white" :class="index===sliderIndex?'red':'secondary'" width="100%">{{ item }}</v-btn>
          </div>
        </v-row>

      </v-container>
    </v-main>
  </v-app>
</template>

<script>

export default {
  name: 'App',

  components: {},

  data: () => ({
    sliderIndex: 0,
    items: [
      'cat',
      'fox',
      'puppy',
      'rabbit',
      'squirrel'
    ]
  }),
  created() {
    setInterval(() => {
      if (this.sliderIndex >= this.items.length - 1) {
        this.sliderIndex = 0;
      } else {
        this.sliderIndex++;
      }
    }, 2500)
  },
  methods: {
    next() {
      if (this.sliderIndex === this.items.length - 1) {
        this.sliderIndex = 0
      } else {
        this.sliderIndex++;
      }
    },
    prev() {
      if (this.sliderIndex === 0) {
        this.sliderIndex = this.items.length - 1;
      } else {
        this.sliderIndex--;
      }
    }
  }
};
</script>
